import React, { Component } from "react";
import { connect } from "react-redux";
import App from "../components/App";
import { createBrowserHistory } from "history";

import {
  getIdProductPreload,
  getAllProductPreload,
  getGlobalError,
  getAuthStatus
} from "../Selectors/common";
import { Switch, Route, BrowserRouter as Router } from "react-router-dom";
import { defaultAuthorization, resetError } from "../store/Actions/User";
const history = createBrowserHistory();
class ROOTContainer extends Component {
  componentDidMount() {
    const { defaultAuthorization } = this.props;
    defaultAuthorization();
  }
  render() {
    const { auth } = this.props;
    return auth !== "unauthorized" ? (
      <Router history={history}>
        <Route render={props => <App {...props} {...this.props} />} />
      </Router>
    ) : null;
  }
}

const mapStatetoProps = state => {
  return {
    preloaderAll: getAllProductPreload(state),
    preloaderID: getIdProductPreload(state),
    auth: getAuthStatus(state),
    errHandler: getGlobalError(state)
  };
};
const getDispatchToProps = dispatch => {
  return {
    defaultAuthorization: () => dispatch(defaultAuthorization()),
    resetError: () => dispatch(resetError())
  };
};

export default connect(
  mapStatetoProps,
  getDispatchToProps
)(ROOTContainer);
